import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { userStore } = this.props.stores;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon } = userStore;
		const { teams } = user;
		const allTeams = userStore.findTeams(teams);
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='row'>
					<div class='col-12'>
						<Error message={emailError} icon={emailErrorIcon} remove={this.resetEmailError} />
                        <Message message={emailMessage} icon={emailMessageIcon} remove={this.resetEmailMessage}  />

						<h5>Din profil</h5>
					</div>
					<div class='col-4 text-muted text-right'>E-post</div><div class='col-8'>{user.email}</div>
					{user.team && <><div class='col-4 text-muted text-right'>Hovedteam</div><div class='col-8'>{userStore.findTeam(user.team)}</div></>}
					{user.teams && <><div class='col-4 text-muted text-right'>Alle teamene</div><div class='col-8'>{allTeams.map(team => (
						<div>
							{team.name}
							{/* <span class='text-muted'>
								<i class='fas fa-dog ml-3' /> {team.dogs.length}
								<i class='fas fa-users ml-3' /> {team.members.length}
							</span> */}
						</div>
					))}</div></>}
					{user.username && <><div class='col-4 text-muted text-right'>Brukernavn</div><div class='col-8'>{user.username}</div></>}
					{user.firstname && <><div class='col-4 text-muted text-right'>Fornavn</div><div class='col-8'>{user.firstname}</div></>}
					{user.lastname && <><div class='col-4 text-muted text-right'>Etternavn</div><div class='col-8'>{user.lastname}</div></>}
					{user.cellphone && <><div class='col-4 text-muted text-right'>Mobil</div><div class='col-8'>{user.cellphone}</div></>}
					{user.url && <><div class='col-4 text-muted text-right'>Hjemmeside</div><div class='col-8'>{user.url}</div></>}
					{user.facebook && <><div class='col-4 text-muted text-right'>Facebook</div><div class='col-8'>{user.facebook}</div></>}
					{user.instagram && <><div class='col-4 text-muted text-right'>Instagram</div><div class='col-8'>{user.instagram}</div></>}
					{user.snapchat && <><div class='col-4 text-muted text-right'>Snapchat</div><div class='col-8'>{user.snapchat}</div></>}
				</div>
			</div>
		);
	}
}

export default Profile;
